@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Zen+Kaku+Gothic+Antique&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Zen+Kaku+Gothic+Antique&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0}.app{text-align:center}
*{margin:0;padding:0}.main-page{background-image:url(/pokedex/static/media/mainPage.9f8bbe8e.jpg);background-size:cover;background-repeat:no-repeat;background-position:center;width:100;height:100vh;font-family:"Fredoka One",cursive;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.main-page__title{font-size:50px;color:#ff0;padding:4px 0 4px 0;animation:glowing 2000ms infinite}@keyframes glowing{0%{text-shadow:0 0 10px orange}50%{text-shadow:0 0 30px red}100%{text-shadow:0 0 10px orange}}.main-page__loading{background-color:#f70633;font-family:"Fredoka One",cursive;color:#ff0;font-size:30px;padding:10px 20px;animation:glowing-button 2000ms infinite;border-radius:10px}@keyframes glowing-button{0%{box-shadow:0 0 10px orange;background-color:#f70633}50%{box-shadow:0 0 30px red;background-color:#f7d306}100%{box-shadow:0 0 10px orange;background-color:#f70633}}
*{margin:0;padding:0}.menu{background-color:#000;width:100;height:100vh;font-family:"Fredoka One",cursive;display:flex;flex-direction:column;align-items:center;justify-content:center}
.menu-button{background-color:#f70633;font-family:"Fredoka One",cursive;color:#ff0;font-size:30px;padding:10px 20px;animation:glowing-button 2000ms infinite;border-radius:10px;margin:15px}@keyframes glowing-button{0%{box-shadow:0 0 10px orange;background-color:#f70633}50%{box-shadow:0 0 30px red;background-color:#f7d306}100%{box-shadow:0 0 10px orange;background-color:#f70633}}
*{margin:0;padding:0}.pokedex{color:#ff0;background-color:#c70848}.pokedex-top{display:grid;grid-template-columns:1.3fr 2fr 1fr;width:100%;height:18vh}.pokedex-top__left{display:flex;justify-content:center;align-items:center;background-color:red;grid-area:1/1/3/2;border-bottom:3px solid #000}.pokedex-top__left__grey-housing{display:flex;justify-content:center;align-items:center;background-color:#fff;width:80%;height:80%;border-radius:50%;border:5px solid linear-gradient(to bottom right, white 0%, white 50%, lightblue 50%, lightblue 100%)}.pokedex-top__left__grey-housing .blue-light{border-radius:50%;border:#000 solid 2px;width:80%;height:80%;animation:glowing-light 2000ms infinite}@keyframes glowing-light{0%{box-shadow:0 0 30px #01dbff;background-color:#01dbff}50%{box-shadow:0 0 30px #9fe4e6;background-color:#9fe4e6}100%{box-shadow:0 0 30px #01dbff;background-color:#01dbff}}.pokedex-top__center-top{display:flex;align-items:center;background-color:red;grid-area:1/2/2/3}.pokedex-top__center-top .red-light{border-radius:50%;background-color:#ee0505;border:2px solid #000;height:25%;width:10%}.pokedex-top__center-top .yellow-light{border-radius:50%;background-color:#ff0;border:2px solid #000;margin:0 3px;height:25%;width:10%}.pokedex-top__center-top .green-light{border-radius:50%;background-color:#23eb23;border:2px solid #000;height:25%;width:10%;animation:glowing-green-light 2000ms infinite}@keyframes glowing-green-light{0%{box-shadow:0 0 30px #23eb23;background-color:#23eb23}50%{box-shadow:0 0 30px #c0ffc0;background-color:#c0ffc0}100%{box-shadow:0 0 30px #23eb23;background-color:#23eb23}}.pokedex-top__right-top{background-color:red;grid-area:1/3/2/4;border-bottom:3px solid #000}.pokedex-top__center-bottom{background:linear-gradient(to bottom right, red 0%, red 48%, black 48%, black 52%, transparent 52%, transparent 100%);grid-area:2/2/3/3}
.pokedexCard{background-color:#f1f1f1;color:#000;height:60vh;width:95%;margin:5px;border:1px solid #000;border-radius:10px 10px 10px 30%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.pokedexCard .pokedexCard__inner{width:80%;height:50%;margin:10px 0;border-radius:10px;border:1px solid #000;background:#00e6fe;background:linear-gradient(to bottom right, #00e6fe 0%, #95e6f5 40%, #aaecf8 50%, #95e6f5 60%, #00e6fe 100%)}.pokedexCard .pokedexCard__inner .pokedexCard__name{font-size:20px}.pokedexCard .pokedexCard__inner .pokedexCard__img{height:90%}.pokedexCard .pokedexCard__inner__header{display:flex;font-size:20px;justify-content:space-between;padding:0 10px;color:#fff;text-shadow:#0295af 1px 0 10px}.pokedexCard .pokedexCard__size{display:flex;justify-content:center;width:100%}.pokedexCard .pokedexCard__size__item{margin:0 10px;padding:2px 10px;background-color:#5f9ea0;border-radius:10px}.pokedexCard .pokedexCard__inner__bug{background:content-box radial-gradient(#1c4b27, #3b9950)}.pokedexCard .pokedexCard__inner__dark{background:content-box radial-gradient(#5a5979, #050707)}.pokedexCard .pokedexCard__inner__dragon{background:content-box radial-gradient(rgba(255, 242, 53, 0.753), rgba(255, 230, 10, 0.87), #ff640a, #fb4b59, #ab1f23)}.pokedexCard .pokedexCard__inner__electric{background:content-box radial-gradient(rgba(255, 242, 53, 0.753), rgba(255, 230, 10, 0.87), #ff640a, #fb4b59, #ab1f23)}.pokedexCard .pokedexCard__inner__fairy{background:content-box radial-gradient(rgba(255, 242, 53, 0.753), rgba(255, 230, 10, 0.87), #ff640a, #fb4b59, #ab1f23)}.pokedexCard .pokedexCard__inner__fighting{background:content-box radial-gradient(rgba(255, 242, 53, 0.753), rgba(255, 230, 10, 0.87), #ff640a, #fb4b59, #ab1f23)}.pokedexCard .pokedexCard__inner__fire{background:content-box radial-gradient(rgba(255, 242, 53, 0.753), rgba(255, 230, 10, 0.87), #ff640a, #fb4b59, #ab1f23)}.pokedexCard .pokedexCard__inner__grass{background:content-box radial-gradient(#b7cebd, #27ca4f, #147b3d)}.pokedexCard .pokedexCard__inner__normal{background:content-box radial-gradient(#dcdfc7, #cb98a7, #74525a, #533b40)}.pokedexCard .pokedexCard__inner__water{background:content-box radial-gradient(#dcdfc7, #86a8fc)}
.pokemonSize{display:flex;justify-content:space-around}.pokemonSize__height{display:flex;flex-direction:column;padding:5px 20px}.pokemonSize__height__label{color:gray;font-size:12px}.pokemonSize__height__number{font-weight:500}.pokemonSize__weight{display:flex;flex-direction:column;padding:5px 20px}.pokemonSize__weight__label{color:gray;font-size:12px}.pokemonSize__weight__number{font-weight:500}
.pokedexCard__types{display:flex}.pokedexCard__types__type{padding:0 10px;margin:0 10px;border-radius:20px;color:#fff}.pokedexCard__types .grass{background-color:#26c850;border:2px solid #137c3e;text-shadow:#6effab 1px 0 10px}.pokedexCard__types .poison{background-color:#996ad9;border:2px solid #602e8c;text-shadow:#d7bcee 1px 0 10px}.pokedexCard__types .fire{background-color:#fd4c5a;border:2px solid #af1f25;text-shadow:#fc7681 1px 0 10px}.pokedexCard__types .flying{background-color:#87a2b5;border:2px solid #7091aa;text-shadow:#b1cfe6 1px 0 10px}.pokedexCard__types .water{background-color:#86a8fc;border:2px solid #1552e2;text-shadow:#5182f7 1px 0 10px}.pokedexCard__types .bug{background-color:#3b9950;border:2px solid #1c4b27;text-shadow:#8beea0 1px 0 10px}.pokedexCard__types .normal{background-color:#ca98a7;border:2px solid #76515c;text-shadow:#ebccd5 1px 0 10px}.pokedexCard__types .dark{background-color:#5a5979;border:2px solid #040706;text-shadow:#b6b5e0 1px 0 10px}.pokedexCard__types .electric{background-color:#fbfb72;border:2px solid #e3e32b;text-shadow:#ffffb2 1px 0 10px}.pokedexCard__types .dragon{background-color:#61cad9;border:2px solid #448b95;text-shadow:#a1dee7 1px 0 10px}.pokedexCard__types .fairy{background-color:#e91369;border:2px solid #971944;text-shadow:#eb6b9e 1px 0 10px}.pokedexCard__types .fighting{background-color:#f06138;border:2px solid #a34426;text-shadow:#f39074 1px 0 10px}.pokedexCard__types .ghost{background-color:#906790;border:2px solid #33336b;text-shadow:#d1a9d1 1px 0 10px}.pokedexCard__types .ground{background-color:#6e491f;border:2px solid #a9702c;text-shadow:#c08a4c 1px 0 10px}.pokedexCard__types .ice{background-color:#d8f0fa;border:2px solid #86d2f5;text-shadow:#fff 1px 0 10px}.pokedexCard__types .psychic{background-color:#f81c91;border:2px solid #a42a6c;text-shadow:#ff8bc9 1px 0 10px}.pokedexCard__types .rock{background-color:#8b3d21;border:2px solid #48180b;text-shadow:#cc7352 1px 0 10px}.pokedexCard__types .steel{background-color:#42bd94;border:2px solid #5f756d;text-shadow:#79ebc5 1px 0 10px}
